<script lang='ts'>
  import Button from './Button.svelte';

  const dataProduct = async() => {
    const dates = await fetch('http://localhost:3000/');
    const data = await dates.json();
    return data
  }

  let promise = dataProduct();
</script>

{#await promise then value}
  {#each value as {name, src, price, mounth}}
    <div class='box-service'>
      <h2>{name}</h2>
      <div class='box-product'>
        <Button drn='right' pos='absolute' ctx='plus' on:click={() => {
          if (mounth < 12) {
            mounth += 1;
            price += 10;
          }}}/>
        <Button pos='absolute' ctx='less' on:click={() => {
          if (mounth > 1) {
            mounth -= 1;
            price -= 10;
          }}}/>
        <img src={src} alt={name} on:click>
      </div>
      <div class='box-info'>
        <span>Price: <span style:font-size=1.6rem>{price}</span></span>
        <span>Mounth: <span style:font-size=1.6rem>{mounth}</span></span>
      </div>
    </div>
  {/each}
{/await}

