import {writable} from 'svelte/store';

let repeat;
let stateTrans = writable('');
let inputCbu = writable('');
let err = writable('');

let valueCbu;
inputCbu.subscribe(val => valueCbu = val);

const sendInfo = async (e) => {
	e.preventDefault();

	const userCbu = new FormData(e.target);
	let cbu;
	if (userCbu && validator(valueCbu)) {
		const sendCbu = await fetch('http://localhost:3000/veryfied', {
			method: 'POST',
			body: userCbu.get('cbu'),
		});
		cbu = await sendCbu.text();
	}

	if (cbu == 'await') repeat = setInterval(veryfied, 5000);
}

const veryfied = async () => {
	const ctx = await fetch('http://localhost:3000/veryfied');
	const value = await ctx.text();
	stateTrans.update(i => i = value);
	if (value == 'true') clearInterval(repeat);
}

//Validate Cbu
let errorCbu = writable();

const cbuLength = cbu => {
	if (cbu.length === 22) {
		errorCbu.update(err => err = undefined);
		err.update(ctx => ctx = '');
		return true;
	}
	errorCbu.update(err => err = 'Incorrect CBU Length');
	err.update(ctx => ctx = 'error');
};

const codeBank = code => {
	if (code.length === 8) {
		let bank = code.substr(0,3);
		let digiteVerifide1 = code[3];
		let sucursal = code.substr(4,3);
 		let digiteVerifide2 = code[7]
		let sum = bank[0] * 7 + bank[1] * 1 + bank[2] * 3 + digiteVerifide1 * 9 + sucursal[0] * 7 + sucursal[1] * 1 + sucursal[2] * 3
 		let diference = 10 - (sum % 10);
		if (diference == digiteVerifide2) return true;
		errorCbu.update(err => err = 'Bank Code Incorrect');
		err.update(ctx => ctx = 'error');
 	}
	return false;
}

const valideAccount = cuenta => {
	if (cuenta.length === 14) {
		let digitoVerificador = cuenta[13];
		let suma = cuenta[0] * 3 + cuenta[1] * 9 + cuenta[2] * 7 + cuenta[3] * 1 + cuenta[4] * 3 + cuenta[5] * 9 + cuenta[6] * 7 + cuenta[7] * 1 + cuenta[8] * 3 + cuenta[9] * 9 + cuenta[10] * 7 + cuenta[11] * 1 + cuenta[12] * 3;
 		let diferencia = 10 - (suma % 10);
		if (diferencia == digitoVerificador) return true;
		errorCbu.update(err => err = 'Account Invalid');
		err.update(ctx => ctx = 'error');
	}
	return false;
}

const validator = (value) => {
	if (/^\d+$/g.test(value)) return cbuLength(value) && codeBank(value.substr(0,8)) && valideAccount(value.substr(8,14));
	errorCbu.update(err => err = 'Only Numbers Are Allowed');
	err.update(ctx => ctx = 'error');
}

export {
	sendInfo,
	stateTrans,
	inputCbu,
	errorCbu,
	err,
}
