<script lang='ts'>
  import BoxProduct from './ProductsBox.svelte';
  import InfoProduct, {closeInfo, Products, scroll} from './ProductsInfo.svelte';

  const names: Array<string> = ['name', 'price', 'mounth', 'src'];

  const passInfoProducts = (e: Event | any) => {
    $closeInfo = 'paybox';
    $scroll = true;

    let box: HTMLDivElement = e.target.parentNode.parentNode;
    let i: number = -1;
    
    i++;
    $Products[names[i]] = box.childNodes[0].textContent;

    box.childNodes[4].childNodes.forEach(num => {
      if (num.textContent !== ' ') {
        const val = num.textContent.match(/\d+/g)[0];
        i++;
        $Products[names[i]] = val;
      };
    });

    box.childNodes[2].childNodes.forEach((img: HTMLImageElement) => {
      if (img.nodeName === 'IMG') {
        i++;
        $Products[names[i]] = img.src;
      }
    });
  };
</script>

<section style='margin-top: 2em;'>
  <BoxProduct on:click={passInfoProducts}/>
  <InfoProduct/>
</section>
