<script lang='ts'>
  import Transfer from './PayTransfers.svelte';
  import Sucess from './PaySucces.svelte';
  import Loading from './PayLoading.svelte';
  import Button from './Button.svelte';
  import { sendInfo, stateTrans} from './validateCbu';
</script>

<script context='module' lang='ts'>
  import { writable } from 'svelte/store';
  export const closePay = writable('');
</script>


<aside class={$closePay}>
  <div>
    <Button
      ctx='close'
      pos='absolute'
      lr={-1.3}
      size={3}
      on:click={() => {
        $closePay = '';
        $stateTrans = '';
      }}
    />
    {#if $stateTrans == 'true'}
      <Sucess/>
    {:else if $stateTrans == 'await'}
      <Loading/>
    {:else}
      <Transfer on:submit={sendInfo}/>
    {/if}
  </div>
</aside>

