<script lang='ts'>
  export let type: string = 'TEXT:';
  export let ctx: string = 'Coppied...';

  let element: HTMLSpanElement;

  const copyText = async (value: HTMLSpanElement) => {
    return await navigator.clipboard.writeText(value.textContent);
  }
</script>

<div>
  <span>{type}</span>
  <span
    style:border='0.13em outset #fff'
    style:border-radius=0.5em
    style:padding='0.2em 0.4em'
    style:color=#fff
    bind:this={element}>{ctx}</span>
  <button on:click={() => copyText(element)}>Copy</button>
</div>

